import React,{useState, useEffect} from "react"
import Axios from "axios"
import "./ActivityLog.css"
import {Alert, Paper, Divider, Typography, Badge} from '@mui/material'
import { DataGrid, GridToolbar} from '@mui/x-data-grid'
import PersonIcon from '@mui/icons-material/Person'

export default function UserLog(props){
    const [ userLogData , setUserLogData] = useState([])

    const columns = [
        { field: 'username', headerName: 'USER', width: 200 },
        { field: 'login_date', headerName: "LOGIN DATE", width: 250 },
        { field: 'logout_date', headerName: "LOGOUT DATE", width: 700 }
    ]

    useEffect(()=>{
        Axios.get(props.testingIn + "/getUserLogs")
        .then((response) => {
            setUserLogData(response.data)
        });
    },[])

    return(
        <div className="ActivityLog">
            <Alert severity="info" className="ActivityLog--welcome" >
                <strong>Note! : </strong>
                All actions made from the database are recorded here.
            </Alert>
            
            <Paper elevation={12} className="ActivityLog--contents">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemStocks--header">
                    <span><PersonIcon fontSize="small" /> 
                        Users Log List
                    </span>
                    <span style={{marginRight: '20px'}}>
                        Number of System User Log:
                        <Badge badgeContent={userLogData.length} style={{marginLeft: '15px'}} color="primary" />
                    </span>
                </Typography>
                <Divider />
                <div className="ActivityLog--body">
                    <div className="ActivityLog--table">
                        <DataGrid
                            getRowId={(userLogData) => userLogData.user_log_id}
                            rows={userLogData}
                            columns={columns}
                            pageSize={8}
                            rowsPerPageOptions={[8]}
                            disableColumnFilter
                            disableColumnSelector
                            disableDensitySelector
                            components={{ Toolbar: GridToolbar }}
                            componentsProps={{
                                toolbar: {
                                    showQuickFilter: true,
                                    quickFilterProps: { debounceMs: 500 },
                                },
                            }} 
                        />
                    </div>
                </div>
            </Paper>
        </div>
    )
}