import React,{useState} from "react"
import "./Sidebar.css"
import AMSLogo from "./Imgs/AMS.png"
import {Paper, List, Divider, ListItemButton, ListItemIcon, ListItemText, Collapse} from '@mui/material/'
import {ExpandLess,ExpandMore, Dashboard} from '@mui/icons-material/'
import DashboardIcon from '@mui/icons-material/Dashboard'
import HomeIcon from '@mui/icons-material/Home'
import ComputerIcon from '@mui/icons-material/Computer'
import FolderIcon from '@mui/icons-material/Folder'
import NotListedLocationIcon from '@mui/icons-material/NotListedLocation'
import AddLocationAltIcon from '@mui/icons-material/AddLocationAlt'
import PinDropIcon from '@mui/icons-material/PinDrop'
import PersonIcon from '@mui/icons-material/Person'
import ReceiptLongIcon from '@mui/icons-material/ReceiptLong'
import StickyNote2Icon from '@mui/icons-material/StickyNote2'
import SearchIcon from '@mui/icons-material/Search'
import WarningSharpIcon from '@mui/icons-material/WarningSharp';

export default function SideBar(props){
    const [showManageItems , setShowManageItems] = useState(false)
    const [showManageLocations , setShowManageLocations] = useState(false)
    const [showSystemLog , setShowSystemLog] = useState(false)

    return (
        <Paper elevation={12} className="Sidebar">
            <img src={AMSLogo} className="Sidebar--logo" />
            <List
                sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}
                component="nav"
            >
                
                <ListItemButton onClick={()=>{props.handleScreen("Dashboard")}}>
                    <ListItemIcon>
                        <HomeIcon />
                    </ListItemIcon>
                    <ListItemText primary="Dashboard"/>
                </ListItemButton>
                <Divider variant="middle" />
                <ListItemButton onClick={()=>{
                    setShowManageItems(!showManageItems)
                    setShowManageLocations(false)
                    setShowSystemLog(false)
                }} >
                    <ListItemIcon>
                        <DashboardIcon />
                    </ListItemIcon>
                    <ListItemText primary="Manage Items"/>
                    {showManageItems ? <ExpandLess /> : <ExpandMore />}
                </ListItemButton>
                <Collapse in={showManageItems} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding >
                        <ListItemButton sx={{ pl: 4 }} onClick={()=>{props.handleScreen("ItemStocks")}}>
                            <ListItemIcon>
                                <ComputerIcon />
                            </ListItemIcon>
                            <ListItemText primary="Items/Stocks" />
                        </ListItemButton>
                        <ListItemButton sx={{ pl: 4 }} onClick={()=>{props.handleScreen("ItemType")}}>
                            <ListItemIcon>
                                <FolderIcon />
                            </ListItemIcon>
                            <ListItemText primary="Item Type" />
                        </ListItemButton>
                        <ListItemButton sx={{ pl: 4 }} onClick={()=>{props.handleScreen("AssignItems")}}>
                            <ListItemIcon>
                                <ComputerIcon />
                            </ListItemIcon>
                            <ListItemText primary="Assign Items" />
                        </ListItemButton>
                        <ListItemButton sx={{ pl: 4 }} onClick={()=>{props.handleScreen("AssignSets")}}>
                            <ListItemIcon>
                                <ComputerIcon />
                            </ListItemIcon>
                            <ListItemText primary="Assign Sets" />
                        </ListItemButton>
                    </List>
                </Collapse>
                    
                <Divider variant="middle" />

                <ListItemButton onClick={()=>{
                    setShowManageLocations(!showManageLocations)
                    setShowManageItems(false)
                    setShowSystemLog(false)
                }} >
                    <ListItemIcon>
                        <NotListedLocationIcon />
                    </ListItemIcon>
                    <ListItemText primary="Manage Locations"/>
                    {showManageLocations ? <ExpandLess /> : <ExpandMore />}
                </ListItemButton>
                <Collapse in={showManageLocations} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding >
                        <ListItemButton sx={{ pl: 4 }} onClick={()=>{props.handleScreen("DeviceLocations")}}>
                            <ListItemIcon>
                                <PinDropIcon />
                            </ListItemIcon>
                            <ListItemText primary="Device Locations" />
                        </ListItemButton>
                        <ListItemButton sx={{ pl: 4 }} onClick={()=>{props.handleScreen("AddLocations")}}>
                            <ListItemIcon>
                                <AddLocationAltIcon />
                            </ListItemIcon>
                            <ListItemText primary="Add Locations" />
                        </ListItemButton>
                    </List>
                </Collapse>   
                <Divider variant="middle" />

                <ListItemButton onClick={()=>{props.handleScreen("SystemUser")}}>
                    <ListItemIcon>
                        <PersonIcon />
                    </ListItemIcon>
                    <ListItemText primary="System User" />
                </ListItemButton>
                <Divider variant="middle" />

                <ListItemButton onClick={()=>{
                    setShowSystemLog(!showSystemLog)
                    setShowManageItems(false)
                    setShowManageLocations(false)
                }} >
                    <ListItemIcon>
                        <ReceiptLongIcon />
                    </ListItemIcon>
                    <ListItemText primary="System Log"/>
                    {showSystemLog ? <ExpandLess /> : <ExpandMore />}
                </ListItemButton>
                <Collapse in={showSystemLog} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding >
                        <ListItemButton sx={{ pl: 4 }} onClick={()=>{props.handleScreen("ActivityLog")}}>
                            <ListItemIcon>
                                <StickyNote2Icon />
                            </ListItemIcon>
                            <ListItemText primary="Activity Log" />
                        </ListItemButton>
                        <ListItemButton sx={{ pl: 4 }} onClick={()=>{props.handleScreen("UserLog")}}>
                            <ListItemIcon>
                                <StickyNote2Icon />
                            </ListItemIcon>
                            <ListItemText primary="User Log" />
                        </ListItemButton>
                    </List>
                </Collapse> 
                <Divider variant="middle" />

                <ListItemButton onClick={()=>{props.handleScreen("AdvanceSearch")}}>
                    <ListItemIcon>
                        <SearchIcon />
                    </ListItemIcon>
                    <ListItemText primary="Advance Search" />
                </ListItemButton>
                <Divider />

                <ListItemButton onClick={()=>{props.handleScreen("DeadPool")}}>
                    <ListItemIcon>
                        <WarningSharpIcon color="error" />
                    </ListItemIcon>
                    <ListItemText primary="DeadPool" />
                </ListItemButton>

            </List>
            
        </Paper>
    )
}