import React,{useState, useEffect} from "react"
import Axios from "axios"
import "./ActivityLog.css"
import {Alert, Paper, Divider, Typography, Badge} from '@mui/material'
import { DataGrid, GridToolbar} from '@mui/x-data-grid'
import PersonIcon from '@mui/icons-material/Person'

export default function ActivityLog(props){
    const [activityLogData , setactivityLogData] = useState([])

    const columns = [
        { field: 'date', headerName: 'DATE', width: 200 },
        { field: 'username', headerName: "USER", width: 250 },
        { field: 'action', headerName: "ACTION", width: 700 }
    ]

    useEffect(()=>{
        Axios.get(props.testingIn + "/getActivityLogs")
        .then((response) => {
            setactivityLogData(response.data)
        });
    },[])

    return(
        <div className="ActivityLog">
            <Alert severity="info" className="ActivityLog--welcome" >
                <strong>Note! : </strong>
                All actions made from the database are recorded here.
            </Alert>
            
            <Paper elevation={12} className="ActivityLog--contents">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemStocks--header">
                    <span><PersonIcon fontSize="small" /> System User Activity Log(s)</span>
                    <span style={{marginRight: '20px'}}>
                        Number of System user Activity Log:
                        <Badge badgeContent={activityLogData.length} style={{marginLeft: '15px'}} color="primary" />
                    </span>
                </Typography>
                <Divider />
                <div className="ActivityLog--body">
                    <div className="ActivityLog--table">
                        <DataGrid
                            getRowId={(activityLogData) => activityLogData.activity_log_id}
                            rows={activityLogData}
                            columns={columns}
                            pageSize={8}
                            rowsPerPageOptions={[8]}
                            disableColumnFilter
                            disableColumnSelector
                            disableDensitySelector
                            components={{ Toolbar: GridToolbar }}
                            componentsProps={{
                                toolbar: {
                                    showQuickFilter: true,
                                    quickFilterProps: { debounceMs: 500 },
                                },
                            }} 
                        />
                    </div>
                </div>
            </Paper>
        </div>
    )
}