import React,{ useState, useEffect} from "react"
import Axios from "axios"
import "./AssignItems.css"
import PlaylistAddCheckIcon from '@mui/icons-material/PlaylistAddCheck';
import {InputLabel, MenuItem, FormControl, Select, Alert, Paper, Divider, Button, Typography, Badge} from '@mui/material'
import { DataGrid, GridToolbar  } from '@mui/x-data-grid'
import EditLocationIcon from '@mui/icons-material/EditLocation'
import AddCircleIcon from '@mui/icons-material/AddCircle'

export default function AssignItems(props){
    const [ assignItemsNotes, setAssignItemsNotes] = useState(true)
    const [ availableItemsData, setAvailableItemsData ] = useState([])
    const [ selectedToTransfer, setSelectedToTransfer ] = useState({
        Location: "",
        item_id: ""
    })
    const [ stLocation, setStLocation ] = useState([])

    useEffect(()=>{
        Axios.get(props.testingIn + "/api/query")
        .then((response) => {
            setAvailableItemsData(response.data)
        });

        Axios.get(props.testingIn + "/location")
        .then((response) => {
            setStLocation(response.data)
        });
    },[])

    const columns = [
        { field: 'dev_name', headerName: 'ITEM TYPE', width: 130 },
        { field: 'dev_desc', headerName: "ITEMS DESCRIPTION", width: 450 },
        { field: 'dev_serial', headerName: "INVENTORY CODE", width: 250 },
        { field: 'dev_brand', headerName: "ITEM'S BRAND", width: 170 },
        { field: 'dev_model', headerName: "MODEL", width: 170 },
        { field: 'dev_status', headerName: "STATUS", width: 130 }
    ]

    const getLocationValue = (event) =>{
        setSelectedToTransfer(prevValue => {
            return{
                ...prevValue,
                Location: event.target.value
            }
        })
    }

    const listofLocations = stLocation.map(prevValue => {
        return (<MenuItem key={prevValue.stdev_id} value={prevValue.stdev_id}>{prevValue.stdev_location_name}</MenuItem>)
    })

    return(
        <div className="AssignItems">
            {assignItemsNotes && 
                <Alert severity="info" className="AssignItems--welcome" onClose={()=> setAssignItemsNotes(prevValue => !prevValue)}>
                    <strong>Note! : </strong>
                    Select from the checbox an Item that you want to assign on a specific location on the dropdown "Location name".
                </Alert>
            }
            <Paper elevation={12} className="AssignItems--contents">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemStocks--header">
                    <span><PlaylistAddCheckIcon fontSize="small" /> Available Item(s) List</span>
                    <span style={{marginRight: '20px'}}>
                        Number of Available Item(s):<Badge badgeContent={availableItemsData.length} style={{marginLeft: '15px'}} color="primary" />
                    </span>
                </Typography>
                <Divider />
                <div className="AssignItems--body">
                    <div className="AssignItems--table">
                        <div className="AssignItems--search">
                            <span className="AssignItems--location">
                                <FormControl sx={{ minWidth: '10vw' }}>
                                    <InputLabel id="demo-simple-select-helper-label">Location</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-helper-label"
                                        id="demo-simple-select-helper"
                                        value={selectedToTransfer.Location}
                                        label="Location"
                                        onChange={getLocationValue}
                                    >
                                        <MenuItem value="">
                                            <em>None</em>
                                        </MenuItem>
                                        {listofLocations}
                                    </Select>
                                    
                                </FormControl>
                                <Button variant="contained" sx={{ padding: '15px 15px' }} color="info"><EditLocationIcon />Assign Location</Button>
                            </span>
                            <span className="AssignItems--adds">
                                <Button variant="contained" color="info" onClick={()=>{props.handleScreen("AddDevice")}}><AddCircleIcon /> Add New Item</Button>
                                <Button variant="contained" color="info"><AddCircleIcon /> Add New Set</Button>
                            </span>
                        </div>
                        <DataGrid
                            getRowId={(availableItemsData) => availableItemsData.id}
                            rows={availableItemsData}
                            columns={columns}
                            pageSize={9}
                            rowsPerPageOptions={[9]}
                            onSelectionModelChange={(newSelectionModel) => {
                                setSelectedToTransfer(prevValue=>{
                                    return{
                                        ...prevValue,
                                        item_id: newSelectionModel[0]
                                    }
                                })
                            }}
                            disableColumnFilter
                            disableColumnSelector
                            disableDensitySelector
                            components={{ Toolbar: GridToolbar }}
                            componentsProps={{
                                toolbar: {
                                    showQuickFilter: true,
                                    quickFilterProps: { debounceMs: 500 },
                                },
                            }}                      
                            checkboxSelection
                        />
                    </div>
                </div>
            </Paper>
        </div>
    )
}