import React,{useState, useEffect} from "react"
import Axios from "axios"
import "./SystemUser.css"
import CategoryIcon from '@mui/icons-material/Category'
import {Modal, Box, Alert, AlertTitle, Paper, Divider, Button, TextField, Typography, Badge} from '@mui/material'
import { DataGrid, GridToolbar } from '@mui/x-data-grid'
import AddCircleIcon from '@mui/icons-material/AddCircle'
import DeleteForeverIcon from '@mui/icons-material/DeleteForever'
import PeopleAltIcon from '@mui/icons-material/PeopleAlt';

export default function SystemUser(props){
    const [ systemUsers , setSystemUsers] = useState([])
    const [ addsystemUsers , setAddSystemUsers] = useState({
        firstname: "",
        lastname: "",
        username: "",
        password: ""
    })
    const [ selectedUser , setSelectedUser ] = useState([])
    const [ systemUserNote , setSystemUserNote ] = useState(true)
    const columns = [
        { 
            field: "fullname", 
            headerName: 'FULL NAME', 
            width: 300 ,
            valueGetter: (params) =>`${params.row.firstname || ''} ${params.row.lastname || ''}`,
        },
        { field: "username", headerName: 'USERNAME', width: 130 },
        { 
            field: 'action', 
            headerName: 'ACTION', 
            width: 130,
            valueGetter: (params) => {<Button variant="contained" color="info"><DeleteForeverIcon />Delete </Button>}
        }
    ]
    const [ addStatus , setAddStatus ] = useState({
        add: "",
        deleted: false
    })
    const [openModal, setOpenModal] = useState(false)
    const [ namesSelected , setNamesSelected ] = useState([])

    useEffect(()=>{
        Axios.get(props.testingIn + "/getSystemUsers")
        .then((response) => {
            setSystemUsers(response.data)
        });
    },[])

    const handleRegister = () => {
        Axios.post(props.testingIn + "/register", addsystemUsers)
        .then((response) => {
            setSystemUsers(response.data.database)
            setAddSystemUsers({
                firstname: "",
                lastname: "",
                username: "",
                password: ""
            })
            setAddStatus({
                add:response.data.result,
                deleted: false
            })
        })
    }

    const deleteSelectedUsers = () => {
        Axios.post(props.testingIn + "/getSystemUsersName", selectedUser)
        .then((response) => {
            setNamesSelected(response.data)
            if(response.data.length > 0) {
                setNamesSelected([])
                Axios.post(props.testingIn + "/getSystemUsersName", selectedUser)
                .then((response) => {
                    console.log(response.data)
                    response.data.map( user_names => {
                        setNamesSelected( prev => {
                            return[
                                ...prev,
                                user_names.username+" "
                            ]
                        })
                    })
                })
                setOpenModal(true) 
            }
        })
    }

    const sureDelete = () => {
        Axios.post(props.testingIn + "/deleteSystemUsersName", selectedUser)
        .then((response) => {
            setSystemUsers(response.data)
            setOpenModal(false)
            setAddStatus({
                add:"",
                deleted: true
            })
        })
    }

    const handleRowClick = (params) => {
        console.log(params.row.username)
    }
    return(
        <div className="SystemUser">
            <Modal
                open={openModal}
                onClose={() => {setOpenModal(false)}}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Alert 
                    severity="error" 
                    sx={{
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        transform: 'translate(-50%, -50%)',
                        width: 400,
                        boxShadow: 24,
                        p: 4,
                    }}
                >
                    <AlertTitle><strong>Warning !</strong> </AlertTitle>
                    <Typography sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        gap: '10px'
                    }}>
                        <span>
                            Are you sure you want to Delete ( {namesSelected.toString()} ) ?
                        </span>
                        <Button 
                            variant="contained" 
                            color="error" 
                            sx={{
                                width: '30%'
                            }}
                            onClick={sureDelete}
                        >
                            <DeleteForeverIcon />
                            Delete
                        </Button>
                    </Typography>
                    
                    
                </Alert>
                
            </Modal>
            <Paper elevation={12} className="SystemUser--addUser">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemType--header">
                    <span><AddCircleIcon fontSize="small" /> Add System User</span>
                </Typography>
                <Divider />
                <span className="SystemUser--addform">
                    <TextField 
                        id="standard-basic" 
                        label="First Name" 
                        variant="outlined" 
                        sx={{
                            width: '90%',
                            marginLeft: '20px'
                        }}
                        value={addsystemUsers.firstname}
                        onChange={(event)=>{
                            setAddSystemUsers( prev => {
                                return{
                                    ...prev,
                                    firstname: event.target.value
                                }
                            })
                        }}
                    />
                    <TextField 
                        id="standard-basic" 
                        label="Last Name" 
                        variant="outlined" 
                        sx={{
                            width: '90%',
                            marginLeft: '20px'
                        }}
                        value={addsystemUsers.lastname}
                        onChange={(event)=>{
                            setAddSystemUsers( prev => {
                                return{
                                    ...prev,
                                    lastname: event.target.value
                                }
                            })
                        }}
                    />
                    <TextField 
                        id="standard-basic" 
                        label="Username" 
                        variant="outlined" 
                        sx={{
                            width: '90%',
                            marginLeft: '20px'
                        }}
                        value={addsystemUsers.username}
                        onChange={(event)=>{
                            setAddSystemUsers( prev => {
                                return{
                                    ...prev,
                                    username: event.target.value
                                }
                            })
                        }}
                    />
                    <TextField 
                        id="standard-basic" 
                        label="Password" 
                        variant="outlined" 
                        sx={{
                            width: '90%',
                            marginLeft: '20px'
                        }}
                        value={addsystemUsers.password}
                        onChange={(event)=>{
                            setAddSystemUsers( prev => {
                                return{
                                    ...prev,
                                    password: event.target.value
                                }
                            })
                        }}
                    />
                    
                    <Button 
                        variant="contained" 
                        color="info" 
                        sx={{
                            width: '30%',
                            marginLeft: '20px'
                        }}
                        onClick={handleRegister}
                    >
                        <AddCircleIcon />
                        Register
                    </Button>
                </span>
                
            </Paper>
            <div className="SystemUser--half">
                {systemUserNote && 
                    <Alert severity="warning" className="SystemUser--welcome" onClose={()=> setSystemUserNote(prevValue => !prevValue)}>
                        <strong>Note! : </strong>
                        Select the checbox if you want to delete
                    </Alert>
                }
                <Paper className="SystemUser--contents" elevation={12}>
                    <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemType--header">
                        <span><PeopleAltIcon fontSize="small" /> System User (s) List</span>
                        <span style={{marginRight: '20px'}}>
                            Number of System user(s):<Badge badgeContent={systemUsers.length} style={{marginLeft: '15px'}} color="primary" />
                        </span>
                    </Typography>
                    <Divider />
                    <div className="SystemUser--body">
                        <div className="SystemUser--delete">
                            <span style={{
                                display: 'flex',
                                alignItems: 'end'
                            }}>
                                <Button 
                                    variant="contained"  
                                    size="large" 
                                    color="error" 
                                    onClick={deleteSelectedUsers}>
                                    Delete
                                </Button>
                            </span>
                            {
                                addStatus.add && 
                                <Alert severity="success" sx={{width: '77%'}}>
                                    <AlertTitle><strong>Success</strong> </AlertTitle>
                                    {addStatus.add}
                                </Alert>
                            }
                            {
                                addStatus.deleted && 
                                <Alert severity="success" sx={{width: '77%'}}>
                                    <AlertTitle><strong>Success</strong> </AlertTitle>
                                    Successfully deleted ( {namesSelected.toString()} ).
                                </Alert>
                            }
                        </div>
                        <div className="SystemUser--table">
                            <DataGrid
                                getRowId={(systemUsers) => systemUsers.admin_id}
                                rows={systemUsers}
                                columns={columns}
                                pageSize={9}
                                rowsPerPageOptions={[9]}
                                checkboxSelection
                                disableColumnFilter
                                disableColumnSelector
                                disableDensitySelector
                                components={{ Toolbar: GridToolbar }}
                                componentsProps={{
                                    toolbar: {
                                        showQuickFilter: true,
                                        quickFilterProps: { debounceMs: 500 },
                                    },
                                }}   
                                onSelectionModelChange={(newSelectionModel) => {
                                    setSelectedUser(newSelectionModel)
                                }} 
                                onRowClick={handleRowClick}
                                enableColorOnDark
                            />
                        </div>
                    </div>
                </Paper>
            </div>
        </div>
    )
}