import React,{useState, useEffect} from "react"
import Axios from "axios"
import "./ItemType.css"
import CategoryIcon from '@mui/icons-material/Category'
import {Modal, Box, Alert, AlertTitle, Paper, Divider, Button, TextField, Typography, Badge} from '@mui/material'
import { DataGrid, GridToolbar } from '@mui/x-data-grid'
import AddCircleIcon from '@mui/icons-material/AddCircle'
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';

export default function ItemType(props){
    const [itemTypeNote , setItemTypeNote] = useState(true)
    const [itemTypeData , setItemTypeData] = useState([{
        dev_id: "",
        dev_name:""
    }])
    const [addDeviceName, setAddDeviceName] = useState({
        dev_name:""
    })
    const [addStatus , setAddStatus] = useState({
        add: "",
        notdelete: [],
        deletedName: []
    })
    const [selectedName , setSelectedName] = useState([])
    const [openModal, setOpenModal] = useState(false)
    const [namestoDelete, setNamestoDelete] = useState([])

    useEffect(()=>{
        Axios.get(props.testingIn + "/devicename")
        .then((response) => {
            setItemTypeData(response.data)
        });
    },[])

    const columns = [
        { field: 'dev_name', headerName: 'ITEM TYPE', width: 500 },
        { field: 'dev_id', headerName: 'ACTION', width: 130 }
    ]

    const handleSubmitDevName =() =>{
        Axios.post(props.testingIn + "/api/addDeviceName", addDeviceName)
        .then((response) => {
            setAddStatus({
                add: response.data.result,
                notdelete: [],
                deletedName: []
            })
            setItemTypeData(response.data.devList)
            setAddDeviceName({dev_name: ""})
        });
    }

    const deleteSelectedNames = () => {
        Axios.post(props.testingIn + "/api/deleteDeviceName", selectedName)
        .then((response) => {
            setItemTypeData(response.data.result)
            setAddStatus({
                add: '',
                notdelete: response.data.notDelete,
                deletedName: response.data.fordeleteIds
            })
        });
        
        
    }

    const sureDelete = () =>{
        Axios.post(props.testingIn + "/confirmDeleteDeviceType",addStatus.deletedName)
        .then((response) => {
            setItemTypeData(response.data)
            setOpenModal(false)
        })
    }

    useEffect(()=>{
        if(addStatus.notdelete.length === 0 && addStatus.deletedName.length > 0){
            setNamestoDelete([])
            Axios.post(props.testingIn + "/devicenameValues",addStatus.deletedName)
            .then((response) => {
                // setNamestoDelete(response.data)
                console.log(response.data)
                response.data.map( dev_names => {
                    setNamestoDelete( prev => {
                        return[
                            ...prev,
                            dev_names.dev_name+" "
                        ]
                    })
                })
            })
            setOpenModal(true)
        }
    },[addStatus])
    
    

    return(
        <div className="ItemType">
            <Modal
                open={openModal}
                onClose={() => {setOpenModal(false)}}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Alert 
                    severity="error" 
                    sx={{
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        transform: 'translate(-50%, -50%)',
                        width: 400,
                        boxShadow: 24,
                        p: 4,
                    }}
                >
                    <AlertTitle><strong>Warning !</strong> </AlertTitle>
                    <Typography sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        gap: '10px'
                    }}>
                        <span>
                            Are you sure you want to Delete ( {namestoDelete.toString()} ) ?
                        </span>
                        <Button 
                            variant="contained" 
                            color="error" 
                            sx={{
                                width: '30%'
                            }}
                            onClick={sureDelete}
                        >
                            <DeleteForeverIcon />
                            Delete
                        </Button>
                    </Typography>
                    
                    
                </Alert>
                
            </Modal>
            
            <Paper elevation={12} className="ItemType--addDevice">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemType--header">
                    <span><AddCircleIcon fontSize="small" /> Add Device Name</span>
                </Typography>
                <Divider />
                <span className="ItemType--addform">
                    <TextField 
                        id="standard-basic" 
                        label="Device Name" 
                        variant="outlined" 
                        sx={{
                            width: '90%',
                            marginLeft: '20px'
                        }}
                        value={addDeviceName.dev_name}
                        onChange={(event)=>{
                            setAddDeviceName({dev_name: event.target.value})
                        }}
                    />
                    <Button 
                        variant="contained" 
                        color="info" 
                        sx={{
                            width: '30%',
                            marginLeft: '20px'
                        }}
                        onClick={handleSubmitDevName}
                    >
                        <AddCircleIcon />
                        Save
                    </Button>
                </span>
                
            </Paper>
            <div className="ItemType--half">
                {itemTypeNote && 
                    <Alert severity="warning" className="ItemType--welcome" onClose={()=> setItemTypeNote(prevValue => !prevValue)}>
                        <strong>Note! : </strong>
                        Select the checbox if you want to delete
                    </Alert>
                }
                <Paper className="ItemType--contents" elevation={12}>
                    <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="ItemType--header">
                        <span><CategoryIcon fontSize="small" /> Item Type(s) List</span>
                        <span style={{marginRight: '20px'}}>
                            Number of All Device:<Badge badgeContent={itemTypeData.length} style={{marginLeft: '15px'}} color="primary" />
                        </span>
                    </Typography>
                    <Divider />
                    <div className="ItemType--body">
                        <div className="ItemType--delete">
                            <span style={{
                                display: 'flex',
                                alignItems: 'end'
                            }}>
                                <Button variant="contained"  size="large" color="error" onClick={deleteSelectedNames}>
                                    Delete
                                </Button>
                            </span>
                            {addStatus.add && 
                                <Alert severity="success" sx={{width: '77%'}}>
                                    <AlertTitle><strong>Success</strong> </AlertTitle>
                                    {addStatus.add}
                                </Alert>
                                }
                            {addStatus.notdelete.length > 0 && 
                                <Alert severity="error" sx={{width: '77%'}}>
                                    <AlertTitle><strong>Cannot Delete these Item Type(s): ( { addStatus.notdelete.toString()} )</strong> </AlertTitle>
                                    Possible Reasons: Still Being Used or a an Item is connected to this type.
                                </Alert>
                            }
                        </div>
                        <div className="ItemType--table">
                            <DataGrid
                                getRowId={(itemTypeData) => itemTypeData.dev_id}
                                rows={itemTypeData}
                                columns={columns}
                                pageSize={9}
                                rowsPerPageOptions={[9]}
                                checkboxSelection
                                disableColumnFilter
                                disableColumnSelector
                                disableDensitySelector
                                components={{ Toolbar: GridToolbar }}
                                componentsProps={{
                                    toolbar: {
                                        showQuickFilter: true,
                                        quickFilterProps: { debounceMs: 500 },
                                    },
                                }}   
                                onSelectionModelChange={(newSelectionModel) => {
                                    setSelectedName(newSelectionModel)
                                }} 
                            />
                        </div>
                    </div>
                </Paper>
            </div>
        </div>
    )
}