import React, {useState} from "react"
import Axios from "axios"
import "./Login.css"
import AMSLogo from "./Imgs/AMS.png"
import {TextField, Paper} from '@mui/material/'
import LoadingButton from '@mui/lab/LoadingButton'
import LoginIcon from '@mui/icons-material/Login'
import LockOpenIcon from '@mui/icons-material/LockOpen';

export default function Login(props){
    const [loginCred, setLoginCred] = useState({
        username : "",
        password : ""
    })
    const [status , setStatus]= useState("")
    const [loading, setLoading] = useState(false)

    const login = () => {
        Axios.post(props.testingIn + "/login", loginCred)
        .then((response) => {
            if( response.data.loggedIn ){
                props.handleLogin(response)
                setLoading(!loading)
            }else{
                setStatus(response.data.message)
            }
        })
    }

    return(
        <div className="Login-form">
            <img src={AMSLogo} className="login--logo"/>
            <h1>WELCOME TO:</h1>
            <h3>Asset Management System(A.M.S.)</h3>
            <Paper elevation={12} style={
                {display: 'felx',
                 flexDirection: 'column',
                 width: '20vw',
                 height: '13vw',
                 padding: '2vw 1vw',
                 borderRadius: '10px'
                }}
            >
                <span style={{display: 'flex', alignItems: 'center', marginLeft: '2.4vw'}}>
                    <LockOpenIcon />
                    <h2>Login</h2>
                </span>
                <TextField
                    id="filled-helperText"
                    label="Username"
                    variant="filled"
                    sx={{minWidth: '15vw', marginBottom: 1}}
                    onChange={(e) => {
                        setLoginCred(prevValue=>{
                        return {
                            ...prevValue,
                            username: e.target.value
                        }
                        })
                    }}
                />
                <TextField
                    id="outlined-password-input"
                    label="Password"
                    type="password"
                    autoComplete="current-password"
                    variant="filled"
                    sx={{minWidth: '15vw', marginBottom: 1}}
                    onChange={(e) => {
                        setLoginCred(prevValue=>{
                        return {
                            ...prevValue,
                            password: e.target.value
                        }
                        })
                    }}
                />
                
                <LoadingButton
                    size="large"
                    color="info"
                    onClick={login}
                    loading={loading}
                    loadingPosition="start"
                    startIcon={<LoginIcon />}
                    variant="contained"
                >
                    <span>Login</span>
                </LoadingButton>
            </Paper>
            
            {status && 
                <div
                    style={{
                        marginTop: '20px',
                        color: 'red'
                    }}
                >
                    {status}
                </div>
            }

        </div>
    )
}