import React,{useState} from "react"
import "./ContentPanel.css"
import Dashboard from "./Dashboard"
import ItemStocks from "./ItemStocks"
import ItemType from "./ItemType"
import AssignItems from "./AssignItems"
import AssignSets from "./AssignSets"
import DeviceLocations from "./DeviceLocations"
import AddLocations from "./AddLocations"
import SystemUser from "./SystemUser"
import ActivityLog from "./ActivityLog"
import UserLog from "./UserLog"
import AddDevice from "./AddDevice"

export default function ContentPanel(props){
    return(
        <div className="ContentPanel">
            {props.currentScreen === "Dashboard" && <Dashboard currentUser={props.currentUser} testingIn = {props.testingIn} />}
            {props.currentScreen === "ItemStocks" && <ItemStocks testingIn = {props.testingIn} />}
            {props.currentScreen === "ItemType" && <ItemType testingIn = {props.testingIn} />}
            {props.currentScreen === "AssignItems" && <AssignItems handleScreen={props.handleScreen} testingIn = {props.testingIn}/>}
            {props.currentScreen === "AssignSets" && <AssignSets testingIn = {props.testingIn} />}
            {props.currentScreen === "DeviceLocations" && <DeviceLocations testingIn = {props.testingIn} />}
            {props.currentScreen === "AddLocations" && <AddLocations testingIn = {props.testingIn} />}
            {props.currentScreen === "SystemUser" && <SystemUser testingIn = {props.testingIn} />}
            {props.currentScreen === "ActivityLog" && <ActivityLog testingIn = {props.testingIn} />}
            {props.currentScreen === "UserLog" && <UserLog testingIn = {props.testingIn} />}
            {props.currentScreen === "AddDevice" && <AddDevice handleScreen={props.handleScreen} testingIn = {props.testingIn} />}
        </div>
    )
}