import React,{useEffect, useState} from "react"
import "./AddDevice.css"
import AddCircleIcon from '@mui/icons-material/AddCircle'
import KeyboardReturnIcon from '@mui/icons-material/KeyboardReturn'
import InfoIcon from '@mui/icons-material/Info'
import Axios from "axios"
import SaveIcon from '@mui/icons-material/Save'
import {Box, InputLabel, MenuItem, FormControl, Select, TextField, Alert, Paper, Divider, Button, Typography} from '@mui/material'


export default function AddDevice(props){
    const [ deviceTypes, setDeviceTypes] = useState([])
    const [ addDeviceData, setAddDeviceData ] = useState({
        dev_id: "",
        dev_brand: "",
        dev_serial: "",
        dev_model: "",
        dev_desc: ""
    })

    console.log(addDeviceData)
    useEffect(()=>{
        // Axios.get("https://ams-server-svu7.onrender.com/devicename")
        Axios.get("http://localhost:3001/devicename")
        .then((response) => {
            setDeviceTypes(response.data)
        });
    },[])

    const listofDeviceTypes = deviceTypes.map( prevValue => {
        return (<MenuItem key={prevValue.dev_id} value={prevValue.dev_id}>{prevValue.dev_name}</MenuItem>)
    })

    const getDevID = (event) =>{
        setAddDeviceData(prevValue => {
            return{
                ...prevValue,
                dev_id: event.target.value
            }
        })
    }

    const handleFormChange = (event) =>{
        const {id , value} = event.target
        setAddDeviceData( prevValue => {
            return{
                ...prevValue,
                [id]: value
            }
        })
    }

    const submitEntry = () => {
        Axios.post("http://localhost:3001/api/addDevice", addDeviceData)
        .then((response) => {
            console.log(response.data.result)
            props.handleScreen("AssignItems")
        });
    }

    return(
        <div className="AddDevice">
            <Paper elevation={12} className="AddDevice--contents">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="dashboard--header">
                    <span>
                        <AddCircleIcon fontSize="small" /> Add Device
                    </span>
                    <span>
                        <Button variant="contained" color="info" onClick={()=>{props.handleScreen("AssignItems")}}>
                            <KeyboardReturnIcon />Back
                        </Button>
                    </span>
                </Typography>
                <Divider />
                <div className="AddDevice--body">
                    <Alert severity="success" className="AddDevice--welcome">
                        <InfoIcon /> Please Fill in required details
                    </Alert>            
                    <Box className="AddDevice--form">
                        <span>Device Type:</span>
                        <FormControl sx={{ minWidth: '10vw' }}>
                            <InputLabel id="demo-simple-select-helper-label">Device Type</InputLabel>
                            <Select
                                labelId="demo-simple-select-helper-label"
                                id="demo-simple-select-helper"
                                value={addDeviceData.dev_id}
                                label="Device Type"
                                onChange={getDevID}
                                required
                            >
                                <MenuItem value="">
                                    <em>None</em>
                                </MenuItem>
                                {listofDeviceTypes}
                            </Select>
                        </FormControl>
                    </Box>

                    <Box className="AddDevice--form">
                        <span>Device Brand:</span>
                        <TextField 
                            label={'Device Brand'} 
                            id="dev_brand" 
                            margin="dense" 
                            onChange={handleFormChange}
                        />
                    </Box>

                    <Box className="AddDevice--form">
                        <span>Inventory Code:</span>
                        <TextField 
                            label={'Inventory Code'} 
                            id="dev_serial"
                            margin="dense" 
                            onChange={handleFormChange}
                        />
                    </Box>

                    <Box className="AddDevice--form">
                        <span>Device Model:</span>
                        <TextField 
                            label={'Device Model'} 
                            id="dev_model" 
                            margin="dense" 
                            onChange={handleFormChange}
                        />
                    </Box>

                    <Box className="AddDevice--form">
                        <span>Description:</span>
                        <TextField
                            id="dev_desc"
                            label="Description"
                            multiline
                            rows={4}
                            onChange={handleFormChange}
                        />
                    </Box>
                    <Box className="AddDevice--form">
                        <Button variant="contained" color="info" onClick={submitEntry}>
                            <SaveIcon /> Save
                        </Button> 
                    </Box>
                    
                </div>
            </Paper>
            
        </div>
    )
}