import React,{useState,useEffect} from "react"
import Axios from "axios"
import "./Dashboard.css"
import DashboardIcon from '@mui/icons-material/Dashboard'
import AccessTimeIcon from '@mui/icons-material/AccessTime'
import ComputerIcon from '@mui/icons-material/Computer'
import LocationCityIcon from '@mui/icons-material/LocationCity'
import EventAvailableIcon from '@mui/icons-material/EventAvailable'
import LaunchIcon from '@mui/icons-material/Launch';
import {Card, CardActions, CardContent, Alert, Paper, Divider, Button, Typography} from '@mui/material'

export default function Dashboard(props){
    const [welcome , setWelcome] = useState(true)
    const [counts , setCounts ] = useState({
        AllStockCount : 0,
        LocationCount : 0,
        AvailableItemsCount : 0
    })

    useEffect(() => {
        Axios.get(props.testingIn + "/stdevices")
        .then((response) => {
            setCounts(preValue => {
                return{
                    ...preValue,
                    AllStockCount : response.data.length
                }
            })
        });

        Axios.get(props.testingIn + "/location")
        .then((response) => {
            setCounts(preValue => {
                return{
                    ...preValue,
                    LocationCount : response.data.length
                }
            })
        });

        Axios.get(props.testingIn + "/availableItems")
        .then((response) => {
            setCounts(preValue => {
                return{
                    ...preValue,
                    AvailableItemsCount : response.data.length
                }
            })
        });
    }, [])

    const [dateState, setDateState] = useState(new Date());
    useEffect(() => {
        setInterval(() => setDateState(new Date()), 1000);
    }, []);


    return(
        <div className="Dashboard">
            {welcome && 
                <Alert severity="success" className="dashboard--welcome" onClose={()=> setWelcome(prevValue => !prevValue)}>
                    Welcome {props.currentUser.firstname} {props.currentUser.lastname} !
                </Alert>
             }
            <Paper elevation={12} className="dashboard--contents">
                <Typography sx={{ fontSize: 14,fontFamily: 'Poppins' }} color="text.secondary" gutterBottom className="dashboard--header" >
                    <span><DashboardIcon fontSize="small" /> Dashboard</span>
                    <span><AccessTimeIcon /> 
                        <span style={{marginRight: "5px"}}>
                            {dateState.toLocaleString('en-US', {
                                hour: 'numeric',
                                minute: 'numeric',
                                second: 'numeric',
                                hour12: true,
                            })} 
                        </span>
                        |
                        <span style={{marginLeft: "5px"}}>
                            {dateState.toLocaleDateString('en-GB', {
                                day: 'numeric',
                                month: 'short',
                                year: 'numeric',
                            })}
                        </span>
                        
                    </span>
                </Typography>
                <Divider />
                <div className="dashboard--body">

                    <Card variant="outlined" sx={{backgroundColor: 'rgb(99,149,237)'}} className="dashboard--boxes" >
                        <CardContent className="dashboard--info" >
                            <ComputerIcon sx={{ fontSize: 150, color: 'white' }} />
                            <div  className="countdetails" >
                                <span>{counts.AllStockCount}</span> 
                                <Typography variant="h6" component="div" sx={{fontFamily: 'Poppins'}}>
                                    All Stocked
                                </Typography >
                            </div >
                        </CardContent>
                        <CardActions className="dashboard--links" sx={{backgroundColor: 'white', color: 'black'}}>
                            <Typography variant="h5" component="div" sx={{ fontSize: 20,fontFamily: 'Poppins'}}>
                                View Details
                            </Typography >
                            <LaunchIcon />
                        </CardActions>
                    </Card>

                    <Card variant="outlined" className="dashboard--boxes" sx={{backgroundColor: 'rgb(99,149,237)'}}>
                        <CardContent className="dashboard--info" >
                            <LocationCityIcon sx={{ fontSize: 150, color: 'white'}} />
                            <div  className="countdetails" >
                                <span>{counts.LocationCount}</span> 
                                <Typography variant="h6" component="div" sx={{fontFamily: 'Poppins'}}>
                                    Location
                                </Typography >
                            </div >
                        </CardContent>
                        <CardActions className="dashboard--links" sx={{backgroundColor: 'white', color: 'black'}}>
                            <Typography variant="h5" component="div" sx={{ fontSize: 20,fontFamily: 'Poppins'}}>
                                View Details
                            </Typography >
                            <LaunchIcon />
                        </CardActions>
                    </Card>

                    <Card variant="outlined" className="dashboard--boxes" sx={{backgroundColor: 'rgb(91,184,92)'}}>
                        <CardContent className="dashboard--info">
                            <EventAvailableIcon sx={{ fontSize: 150, color: 'white' }} />
                            <div  className="countdetails" >
                                <span>{counts.AvailableItemsCount}</span> 
                                <Typography variant="h6" component="div" sx={{fontFamily: 'Poppins'}}>
                                    Available Items
                                </Typography >
                            </div >
                        </CardContent>
                        <CardActions className="dashboard--links" sx={{backgroundColor: 'white', color: 'black'}}>
                            <Typography variant="h5" component="div" sx={{ fontSize: 20,fontFamily: 'Poppins'}}>
                                View Details
                            </Typography >
                            <LaunchIcon />
                        </CardActions>
                    </Card>

                </div>
            </Paper>
            
        </div>
    )
}